#color-selector{
    width: 300px;
}
.p-input{
    border: dotted 1px lightslategrey !important;
    border-left: none !important;
}
.p-first{
    border-left: dotted 1px lightslategrey !important;
    border-top-left-radius: 4px !important;
    border-bottom-left-radius: 4px !important;
}
.form-control{
    height:40px;
    padding-top: 10px;
}
.p-instruction{
    text-indent: 20px;
}
.input-number{
    color:rgb(214, 94, 73);
    width: 80px;
    text-align: center;
    border: dotted 1px lightslategrey !important;
    border-color: rgb(214, 94, 73) !important;
    border-radius: 10px;
}
.input-number::placeholder{
    color:rgba(214, 94, 73, 0.4);
}
.input-number:focus{
    border-style: solid !important;
}
input[type='number']::-webkit-inner-spin-button, 
input[type='number']::-webkit-outer-spin-button { 
    -webkit-appearance: none;
    margin: 0;
}
.label-ul-title{
    color: rgb(214, 94, 73);
    letter-spacing: 2px;
}
.checkbox-color-filter:checked + .color-filter{
    box-shadow: none !important;
    -webkit-box-shadow: none !important;
}
#instruction{
    max-height: 230px;
    width: auto;
	margin-left: auto;
	margin-right: auto;
    display: block;
    padding: 40px;
    box-shadow: 0px 0px 5px lightgray;
    cursor: pointer;
}
#instruction2{
	margin-left: auto;
	margin-right: auto;
    max-height: 130px;
    display: none;
}
.confirm_card-title{
    text-align: center;
    letter-spacing: 5px;
    font-weight: bold;
}
.confirm_card{
    display: block;
	margin-left: auto;
	margin-right: auto;
    cursor: context-menu;
    line-height: 30px;
    padding: 20px;
    box-shadow: 0px 0px 5px lightgray;
}
.confirm_list li{
    text-indent: 30px;
}
.confirm_list li strong{
    color: rgb(214, 94, 73);
}
@media only screen and (max-width: 800px) {
#instruction{
        display: none;
}
#instruction2{
        display: block;
}
}





















































































































































